html {
  box-sizing: border-box; }

*,
*:before,
*:after {
  box-sizing: inherit; }

body {
  align-items: center;
  background: #E2E3E5;
  display: flex;
  font-family: "Dynalight", cursive;
  justify-content: center;
  min-height: 100vh;
  padding: 16px; }

.editor__button {
  display: flex;
  justify-content: flex-end; }

.editor__button--load {
  width: 96px;
  height: 40px;
  background: #f4f841;
  border: none;
  box-shadow: 0px 6px 10px rgba(244, 248, 65, 0.4);
  border-radius: 20px 0px;
  font-size: 22px;
  line-height: 20px;
  letter-spacing: 1px;
  color: #1424AE;
  text-transform: uppercase;
  outline-color: #E2E3E5;
  transition: background-color 0.2s ease-out;
  margin-left: 24px; }

.editor__button--load:hover {
  background: #f7f8ab; }

.editor__button--remove {
  width: 128px;
  height: 40px;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  font-size: 22px;
  line-height: 20px;
  border: none;
  border-radius: 20px 0px;
  letter-spacing: 1px;
  color: #1424ae;
  background: #f84141;
  box-shadow: 0px 6px 10px rgba(248, 65, 65, 0.4);
  text-transform: uppercase;
  margin-left: 24px;
  margin-right: 40px;
  outline-color: #E2E3E5;
  transition: background-color 0.2s ease-out; }

.editor__button--remove:hover {
  background: #fa6a6a; }

.editor__textarea-wrapper {
  margin: 0; }

.editor__button--save {
  width: 96px;
  height: 40px;
  background: #1424AE;
  box-shadow: 0px 6px 10px rgba(20, 36, 174, 0.4);
  font-size: 22px;
  line-height: 20px;
  letter-spacing: 1px;
  color: #f4f841;
  text-transform: uppercase;
  border-radius: 20px 0px;
  border: none;
  outline-color: #E2E3E5;
  transition: background-color 0.2s ease-out; }

.editor__button--save:hover {
  background: #164ecf; }

.editor__header {
  color: #1424AE;
  font-weight: normal;
  font-size: 48px;
  line-height: 64px;
  margin-bottom: 9px;
  text-indent: 24px;
  text-shadow: 0px 2px 5px rgba(20, 36, 174, 0.8);
  transform: rotate(-0.1deg); }

.editor__form {
  background: linear-gradient(228.26deg, #15006b 20.98%, rgba(12, 53, 196, 0.5625) 55.56%, rgba(14, 11, 159, 0.25) 99.1%);
  border-radius: 5px;
  height: 336px;
  margin-bottom: 16px;
  max-width: 544px;
  padding: 24px;
  width: 100%; }

.editor__textarea {
  width: 496px;
  height: 288px;
  font-size: 28px;
  line-height: 24px;
  color: #3f51b5;
  outline-color: aliceblue;
  padding: 16px; }
